import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "../../styles/main-privacy.css"
import "../../styles/main-article-column.css"

export default function Home() {
  return (
    <Layout>
      <Seo title="コラム" />
      <div className="wrap">
        <section className="mv_ac-cm">
          <div className="mv_ac-cm-inner">
            <p className="txt1"><span>コラム</span></p>
            <p className="txt2">小さな会社の経営者様にむけて<br/>ちょっと役に立つ、<br/>ちょっと面白い、<br/>そんなお話をご紹介します。</p>
          </div>
        </section>

        <section className="ac-cm">
          <h2>株式会社ホットソウルのホームページリニューアルしました！</h2>
          <div className="ac-cm-cont">
            <div className="ac-cm-inner">
              <div className="item-box">
                <p className="postdate">2021.10.2</p>
                <div className="item no-border first">
                  <div className="large-img">
                    <StaticImage
                      src="../../images/main-column/editors-note-20211002/ph1.jpg"
                      alt="株式会社ホットソウルのホームページリニューアルしました！"
                      placeholder="blurred"
                    />
                  </div>
                  <p>
                    webディレクターの三島奈津美さん、デザイナーの前田義暢さん、ライティングを担当した私（小宮雅哲）、
                    そして全体統括とコーディングも担当した若杉廣さんの4人で協力して、
                    株式会社ホットソウルのホームページをリニューアルしました！<br/>
                    それぞれこだわったポイントや苦労した点を聞いてみましょう！
                  </p>
                </div>

                <div className="item">
                  <div className="flex-support">
                    <div className="flex-support-item left">三島さん：</div>
                    <div className="flex-support-item right">
                      ディレクションと、コーディングも半分担当した三島です。<br/>
                      自分の中で、このサイトの役割は「ユーザーとホットソウルをマッチングさせること」だと考えていました。
                      ユーザーさんって、いろんな会社を比較して依頼先を決めると思うんですよね。
                      今回制作したサイトは、最初の段階から「デジタル化に困っている」「神奈川の」「小さな会社の経営者」にユーザー像を絞れたことで、
                      本当に求めている人に届くサイトになったんじゃないかなと思っています。<br/>
                      コロナによって、ITやリモートは今後ますます需要が増えるはずです。
                      時代に応じて柔軟に対応していくことが必要なので、私自身も「昨日と同じ自分じゃいけない！」と気を引き締めて、
                      これからも頑張っていきます。
                    </div>
                  </div>
                  <div className="small-img">
                    <StaticImage
                      src="../../images/main-column/editors-note-20211002/ph2.jpg"
                      alt="三島さん"
                      placeholder="blurred"
                    />
                  </div>
                </div>

                <div className="item">
                  <div className="flex-support">
                    <div className="flex-support-item left">前田さん：</div>
                    <div className="flex-support-item right">
                      デザインを担当した前田です。<br/>
                      今回は直接、クライアントでもある若杉さんといろいろディスカッションできたので、とてもやりやすかったです。<br/>
                      ITはどうしても硬いイメージがあったので、ITに疎い方が見ても受け入れてもらえる「やさしいデザイン」にすることがテーマでした。
                      暖色系の色使いと、イラストを使用することで柔らかい雰囲気が出せたと思います。
                      また、情報を整理することで、シンプルで読みやすいサイトにすることを心がけました。
                      若杉さんのやさしい人柄も表現できていると自負しています。<br/>
                      また、このサイトだけでなく、名刺のデザインも新たに制作させていただきました。
                      全体的に統一感のあるデザインで、若杉さんの今後のご活躍に少しでもお役に立てれば光栄です。
                    </div>
                  </div>
                  <div className="small-img">
                    <StaticImage
                      src="../../images/main-column/editors-note-20211002/ph3.jpg"
                      alt="前田さん"
                      placeholder="blurred"
                    />
                  </div>
                </div>

                <div className="item">
                  <div className="flex-support">
                    <div className="flex-support-item left">小宮：</div>
                    <div className="flex-support-item right">
                      テキスト作成のお手伝いをした小宮です。<br/>
                      このプロジェクトに携わる前は、ITやDXというと、自分とは違う遠い世界の出来事のような気がしていましたし、
                      はっきり言って苦手でした。（笑）<br/>
                      ところが、いろいろと調べていくうちに、「実はパソコンやインターネットを使えば、なんでもITなんじゃないか？」
                      と自分なりの解釈ができ、心のハードルが下がって、少し身近に感じられるようになりました。<br/>
                      「苦手」と感じるのは、「知らない」だけのことかもしれません。
                      ITやDXに苦手意識を持っている方が、このサイトを通じて、少しでも親近感を持ってくれるようになったら、とても嬉しく思います。
                    </div>
                  </div>
                  <div className="small-img">
                    <StaticImage
                      src="../../images/main-column/editors-note-20211002/ph4.jpg"
                      alt="小宮さん"
                      placeholder="blurred"
                    />
                  </div>
                </div>

                <div className="item">
                  <div className="flex-support">
                    <div className="flex-support-item left">若杉さん：</div><br/>
                    <div className="flex-support-item right">
                      このプロジェクトのオーナーの若杉です。<br/>
                      三島さん、前田さん、小宮さん、カメラマンの新井さんはじめ、皆様のご協力のおかげで、素晴らしいHPができました。
                      本当にありがとうございました。<br/>
                      今回こだわった点は、見た目の派手さ豪華さよりマーケティング効果を重視したところです。
                      最初の打合せの後、三島さんからトップページをランディングページと同様に考える構成案を頂き、
                      それでサイトの方向性がビシッと決まったと思います。<br/>
                      サイトの主目的はデジタル戦略室サービスの認知度向上なのですが、やさしいDX推進協議会の活動を広めたいという目的も少しあります。<br/>
                      ITエンジニアの中途採用も随時募集しているので、このサイトを見て面白そうな会社だと興味を持ってもらえるとうれしいです。<br/>
                      今後もこのサイトを育てつつ、事業を拡大しながら、地域や社会に貢献していきたいと思います。
                    </div>
                  </div>
                  <div className="small-img">
                    <StaticImage
                      src="../../images/main-column/editors-note-20211002/ph5.jpg"
                      alt="若杉さん"
                      placeholder="blurred"
                    />
                  </div>
                </div>

                <div className="item no-border">
                  <div className="large-img">
                    <StaticImage
                      src="../../images/main-column/editors-note-20211002/ph6.jpg"
                      alt="株式会社ホットソウルのホームページリニューアルしました！"
                      placeholder="blurred"
                    />
                  </div>
                </div>

                <div className="item">
                  <div className="flex-writer">
                    <div className="flex-writer-item left">
                      <p>この記事を書いた人</p>
                      <StaticImage
                        src="../../images/main-column/editors-note-20211002/icon-writer1.jpg"
                        alt="この記事を書いた人"
                        placeholder="blurred"
                      />
                    </div>
                    <div className="flex-writer-item right">
                      <p className="name">小宮 雅哲</p>
                      <p>
                        元・CMディレクター、映画監督、コピーライター。<br className="pc"/>
                        動画制作とHP制作で、企業の収益UPをサポートします。<br className="pc"/>
                        多くの人が自分らしく生きることで、暮らしやすい社会を目指しています。<br className="pc"/>
                        横浜在住。介護職初任者研修の資格あり。<br/>
                        （合同会社 竜宮小宮　業務執行社員）
                      </p>
                    </div>
                  </div>
                </div>

                <div className="flex-share">
                  <ul className="sns">
                    <li>
                      <a href="https://twitter.com/share?url=https://www.hotsoul.jp/column/editors-note" rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../../images/icon-tw.png"
                          alt="twitterシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li> 
                    <li>
                      <a href="http://www.facebook.com/share.php?u=https://www.hotsoul.jp/column/editors-note" rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../../images/icon-fb.png"
                          alt="facebookシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li>
                    <li>
                      <a href="https://social-plugins.line.me/lineit/share?url=https://www.hotsoul.jp/column/editors-note" rel="nofollow noopener noreferrer" target="_blank">
                        <StaticImage
                          src="../../images/icon-line.png"
                          alt="LIMEシェア"
                          placeholder="blurred"
                        />
                      </a>
                    </li>
                  </ul>
			          </div>
                <br/>                
                <p className="archive"><Link to={`/column`}>他の記事も読む</Link></p>

              </div>                
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}